#container {
    transition: .25s;
    margin-top: -250px;
    margin-bottom: 50px;
}

@media screen and (max-width: 768px) {
    #container #card {
        margin-top: 20px;
    }
}

div#card {
    color: white;
    display: block;
    align-items: center;
    background-color: #2c2c2c;
    border-radius: 10px;
    padding: 50px;
    cursor: default;
    box-sizing: border-box;
    position: relative;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.25);
}

div#card h1 {
    font-size: 28px;
    font-weight: bold;
}

div#card p {
    font-size: 16px;
    font-weight: 400;
    color: #fafafa;
}

div#image-card {
    overflow: hidden;
    background-color: #2c2c2c;
    border-radius: 10px;
    box-sizing: border-box;
    position: relative;
    display: block;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.25);
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

div#image-card img {
    width: 100%;
    max-height: 350px !important;
    object-fit: cover;
    object-position: top;
    height: 340px;
    transition: .25s;
}

div#image-card:hover img {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

div#image-card #info {
    padding: 20px;
    position: absolute;
    top: 0;
    left: 0;
    color: #fff;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background: linear-gradient(to bottom, rgba(19, 165, 196, 0) 0%, rgba(19, 165, 196, 0) 40%, rgba(0, 0, 0, 0.65) 89%, rgba(0, 0, 0, 0.75) 100%);
    text-transform: uppercase;
    line-height: 3px;
}

div#image-card #info h1 {
    font-size: 26px;
    text-shadow: 0 2px 0px rgba(0, 0, 0, 0.25);
    font-weight: 800;
    color: white;
    text-transform: capitalize;
}

div#image-card #info:hover {
    text-decoration: none;
    color: white;
    cursor: default;
}

div#image-card #info p {
    font-size: 16px;
    text-shadow: 0 2px 0px rgba(0, 0, 0, 0.25);
    font-weight: 300;
}
div#scrollable-image-card {
    overflow: hidden;
    background-color: #2c2c2c;
    border-radius: 10px;
    box-sizing: border-box;
    position: relative;
    display: block;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.25);
}

div#scrollable-image-card img {
    width: 100%;
    max-height: 350px !important;
    object-fit: cover;
    object-position: top;
    height: 340px;
    transition: object-position 2s ease-in-out;
}

div#scrollable-image-card:hover img {
    object-position: 0px 100%;
}

div#scrollable-image-card #info {
    padding: 20px;
    position: absolute;
    top: 0;
    left: 0;
    color: #fff;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background: linear-gradient(to bottom, rgba(19, 165, 196, 0) 0%, rgba(19, 165, 196, 0) 40%, rgba(0, 0, 0, 0.65) 89%, rgba(0, 0, 0, 0.75) 100%);
    text-transform: uppercase;
    line-height: 3px;
}

div#scrollable-image-card #info h1 {
    font-size: 26px;
    text-shadow: 0 2px 0px rgba(0, 0, 0, 0.25);
    font-weight: 800;
    color: white;
    text-transform: capitalize;
}

div#scrollable-image-card #info:hover {
    text-decoration: none;
    color: white;
    cursor: default;
}

div#scrollable-image-card #info p {
    font-size: 16px;
    text-shadow: 0 2px 0px rgba(0, 0, 0, 0.25);
    font-weight: 300;
}

#container {
    transition: .25s;
    margin-top: -250px;
    margin-bottom: 50px;
}

#work-table {
    margin-top: 65px;
    display: flex;
    flex-wrap: wrap;
}

#work-table #item {
    margin-bottom: 43px;
}

#discord-icon {
    margin-top: 10px;
}

* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif !important; 
    background: #232323 !important;
    color: #FFF;
}
header {
    transition: .25s;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 400px;
    line-height: 158px;
}

header:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 75%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 75%, 0 100%);
    background: rgb(59, 38, 59);
    background: linear-gradient(to bottom, rgba(59, 38, 59, 1) 0%, rgba(34,21,34,1) 100%);
}

header #logo {
    display: inline-block;
    text-transform: uppercase;
    color: white;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 5px;
    position: relative;
    text-shadow: 0 2px 0px rgba(0, 0, 0, 0.25);
    text-decoration: none;
}

header #logo:before {
    content: "";
    display: inline-block;
    box-sizing: border-box;
    margin-top: 2px;
    height: 150px;
    float: left;
    width: 40px;
    background: url(/vitagliano-portfolio/static/media/logo.4a239189.png) no-repeat center/100% auto;
}

/* nav items */
div#nav-items {
    float: right;
    display: inline-block;
    position: relative;
}

/* hamburguer menu */
div.hamburger {
    display: none;
    position: relative;
    background-color: #FCFCFC;
    height: 6px;
    width: 50px;
    margin: 75px 1px 0 0;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
    transition: 0.5s;
    cursor: pointer;
}

div.hamburger::before {
    top: -16px;
}

div.hamburger::after {
    top: 16px;
}

div.hamburger.nav-toggler::before,
div.hamburger.nav-toggler::after {
    content: '';
    position: absolute;
    width: 50px;
    height: 6px;
    background-color: #FCFCFC;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: background-color 0.5s,
        -webkit-transform 0.7s;
    transition: transform 0.7s,
        background-color 0.5s;
    transition: transform 0.7s,
        background-color 0.5s,
        -webkit-transform 0.7s;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

div.hamburger.nav-toggler.true {
    background-color: rgba(0, 0, 0, 0);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0);
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}

div.hamburger.nav-toggler.true::before {
    top: 0;
    -webkit-transform: scale(2) rotate(135deg);
            transform: scale(2) rotate(135deg);
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
}

div.hamburger.nav-toggler.true::after {
    top: 0;
    -webkit-transform: scale(2) rotate(225deg);
            transform: scale(2) rotate(225deg);
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
}

/* toggle hamburguer menu on mobile devices */
@media screen and (max-width: 768px) {
    div.hamburger {
        display: flex;
    }
}

a#nav-item {
    display: inline-block;
    margin-left: 40px;
    list-style: none;
}

a#nav-item:hover #icon {
    text-decoration: none;
    color: #fcde4a;
}

a#nav-item:hover span {
    text-decoration: none;
    color: #fcde4a;
}

a#nav-item span {
    font-family: 'Poppins', sans-serif;
    display: inline-block;
    color: white;
    position: relative;
    font-size: 18px;
    font-weight: 700;
    text-shadow: 0 2px 0px rgba(0, 0, 0, 0.25);
    text-decoration: none;
    transition: all ease .5s;
}

a#nav-item #icon {
    float: left;
    margin: 65px 10px;
    color: white;
    transition: all ease .5s;
}

@media screen and (max-width: 768px) {
    a#nav-item {
        display: none;
    }
}

